import React, { useState } from 'react'
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { getMyProfile, loadUser } from '../../Actions/User';
import ProfileDetail from './MyProfileDetail/ProfileDetail';
import ProfileHeader from './MyProfileHeader/ProfileHeader'
import './myProfile.css'
import Post from '../Home/Post/Post'
import Loder from '../Loder/Loder';
import { NavLink } from 'react-router-dom';
import GifLoader from '../Loder/GifLoader';
import BigGifLoader from '../Loder/BigGifLoader';

const MyProfile = ({topLoader}) => {

    const dispatch = useDispatch();
    const posts = useSelector(state => state.myProfile.myProfile)
    const me = useSelector(state => state.user.user)
    const [isLoader, setIsLoader] = useState(false)
    
    

    
    useEffect(()=>{
      if(!me){
        
        setIsLoader(true)
        dispatch(loadUser())
        setIsLoader(false)
      }

      const fetchData = async () =>{
        if(!posts){
          topLoader(40)
          setIsLoader(true)
          await dispatch(getMyProfile())
          setIsLoader(false)
        }
      }    
      fetchData()
      topLoader(100)
  }, [dispatch])

  return (
    <div style={{minHeight: "100vh" ,backgroundColor: "#000000"}}>
        <ProfileHeader />
        <ProfileDetail />
        
        {
          isLoader === true ? <BigGifLoader /> :

          posts &&
          <div className="all-explore-posts">
          {
            posts.post.map((p)=>(
                <NavLink to={`/post/myProfile/${p._id}`} className='explore-image-div'>
                  <img src={p.image.public_url} alt="" className='img-explore'/>
                </NavLink>
            ))
          }
        </div>
        }
        
    </div>
  )
}

export default MyProfile