import React, { useEffect, useState } from 'react'
import ClearIcon from '@mui/icons-material/Clear';
import '../../MyFollowers/myFollower.css'
import { useDispatch, useSelector } from 'react-redux';
import {NavLink, useNavigate, useParams } from 'react-router-dom';
import User from '../../Home/Users/User';
import { getFriFollowings } from '../../../Actions/User';
import Loder from '../../Loder/Loder';
import BigGifLoader from '../../Loder/BigGifLoader';

const FriFollowings = ({topLoader}) => {

    const params = useParams();
    const dispatch = useDispatch();
    const followers = useSelector(state => state.friFollowings.followings )
    const navigate = useNavigate(); 
    const [isLoader, setIsLoader] = useState(false)

    useEffect(()=> async function(){
        topLoader(45)
        setIsLoader(true)
        await dispatch(getFriFollowings(params.id))
        topLoader(100)
        setIsLoader(false)
    }, [dispatch])

  return (
    <>
       { 
        isLoader === true ? <BigGifLoader /> :
       <div className="followers-conatiner">
        <div className="likeby-header">
            <div className="back-likedby" onClick={() => navigate(-1)}>
                <ClearIcon className='close-likedBy'/>
            </div>
            <div className="likedBy-head">
               Followings
            </div>
        </div>

        <div className='all-liked-user'>
            {
                followers && 
                   followers.map((user)=>(
                    <NavLink to={`/friend/profile/${user._id}`} className='links'>
                        <User
                            avatar={user.avatar.url}
                            userName={user.username}
                            userId={user._id}
                        /> 
                    </NavLink>
                   ))
            }     
        </div>
        </div>}
    </>
  )
}

export default FriFollowings