import axios from "axios"
const message = ''

export const loginUser = (username, password) => async (dispatch)=> {
    try {

        dispatch({
            type: "LoginRequest"
        })
        console.log("req")
        const {data} = await axios.post("/api/v1/user/login",{username, password},  {
            withCredentials: 'include',
            headers:{
                "Content-Type": "application/json"
            }
        })
        console.log(data)
        if(data){
            console.log("if")
            await localStorage.setItem("user._id",data.user._id.toString())
        }
        console.log("succ")
        dispatch({
            type: "LoginSuccess",
            payload: data.user,
            message: data.message
        })

    } catch (error) {
        dispatch({
            type: "LoginFailuer",
            payload: error,
            message: error.message
        })
    }
}

export const signinUser = (username, password, email, avatar) => async (dispatch)=> {
    try {

        dispatch({
            type: "SigninRequest"
        })

        const {data} = await axios.post("/api/v1/user/signin", {username, password, email, avatar}, {
            headers:{
                "Content-Type": "application/json"
            }
        })

        await localStorage.setItem("user._id",data.user._id.toString())

        dispatch({
            type: "SigninSuccess",
            payload: data.user
        })

    } catch (error) {
        dispatch({
            type: "SigninFailuer",
            payload: error,
            message: error.message
        })
    }
}

export const logOutUser = () => async(dispatch) =>{
    try {

        dispatch({
            type: "LogoutRequest",
        })

        await axios.get('/api/v1/user/logout');

        await localStorage.clear();

        dispatch({
            type: "LogoutSuccess",
        })
        
    } catch (error) {
        dispatch({
            type: "LogoutFailuer",
            payload: error
        })
    }
}

export const loadUser = () => async(dispatch) =>{
    try {
        dispatch({
            type: "LodingRequest"
        })

        const {data} = await axios.get('/api/v1/me')

        await localStorage.setItem("user._id",data.user._id.toString())

        dispatch({
            type: "LodingSuccess",
            payload: data.user
        })

    } catch (error) {
        dispatch({
            type: "LodingFailuer",
            payload: error
        })
    }
}

