import React from "react";
import ClearIcon from "@mui/icons-material/Clear";
import { useNavigate, useParams, useHistory } from "react-router-dom";
import "./comment.css";
import { Avatar } from "@mui/material";
import DeleteOutlineOutlinedIcon from "@mui/icons-material/DeleteOutlineOutlined";
import FriComment from "./FriComment/FriComment";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { getAllComments, pushCommnet, updateExpPostCom, updateFirPostCom, updateFollowingPostCom } from "../../Actions/Post";
import "./comment.css";
import { useState } from "react";

const Comment = ({topLoader}) => {
  const navigate = useNavigate();
  const params = useParams();
  const dispatch = useDispatch();

  const comments = useSelector((state) => state.postAllComments.comments);
  const caption = useSelector((state) => state.postAllComments.caption);
  const accountOwner = useSelector((state) => state.user.user);
  const postOwner = useSelector((state) => state.postAllComments.owner)
  const ownerName = useSelector((state) => state.postAllComments.ownerName)
  const ownerImage = useSelector((state) => state.postAllComments.ownerImage)
  const {post} = useSelector(state => state.followingPost)
  const expPosts = useSelector((state) => state.explorePost.posts);
  const singPost = useSelector(state => state.singlePost.post)
  


  const [createComment, setCreateComment] = useState("")
  const [isComment, setIsComment] = useState(false)
  const [newComment, setNewComment] = useState("")
  var postId = params.postId;


  useEffect(() =>{
        const fetchData = async ()=>{
          topLoader(45)
          await dispatch(getAllComments(params.postId));
          topLoader(100)
        }
        fetchData();
    },[dispatch, isComment]);

  const pushComment = async() =>{
    if(createComment !== ""){
        topLoader(45)
        const userId = localStorage.getItem('user._id')
        await dispatch(pushCommnet(params.postId, createComment))
        setIsComment(true);
        setNewComment(createComment)
        if(params.comp === "followUser"){
          dispatch(updateFollowingPostCom(post,params.postId,userId, createComment))
        }
        else if(params.comp === "sugPosts"){
          console.log("in fun")
          dispatch(updateExpPostCom(expPosts, params.postId,userId,createComment ))
        }
        else if(params.comp === "singPost"){
          dispatch(updateFirPostCom(singPost, params.postId,userId, createComment))
        }
        topLoader(100)
        setCreateComment('')
        document.getElementById('new-comment').style.display = "flex";
    } else {
        window.alert("not allow empty comment")
    }
  }

  return (
    <>
      <div className="comment-container">
        <div className="likeby-header">
          <div className="back-likedby" onClick={() => navigate(-1)}>
            <ClearIcon className="close-likedBy" />
          </div>
          <div className="likedBy-head">Comments</div>
        </div>
        {
        accountOwner && accountOwner._id !== postOwner ?
        <div className="create-comment">
          <div className="owner-comment-avatar">
            <Avatar src={accountOwner.avatar.url} />
          </div>
          <div className="comment-input">
            <input
              type="text"
              className="input-comment-text"
              placeholder="Add a comment..."
              value={createComment}
              onChange={(e)=> setCreateComment(e.target.value)}
            />
            <span className="comment-btn" onClick={pushComment}>Post</span>
          </div>
        </div> : null
        }
        <hr color="black" />


        <div className="all-comment">
          {ownerName && ownerImage && caption !== "" ? (
            <div className="post-owner">
              <Avatar src={ownerImage} className="ownwr-img" />
              <span className="post-ownwr-id">{ownerName}</span>
              <span className="ownwr-comment">{caption}</span>
            </div>
          ) : null}

            {
                isComment === true ? 
                <div className="post-owner" id="new-comment">
                <Avatar src={accountOwner.avatar.url} className="ownwr-img" />
                <span className="post-ownwr-id">{accountOwner.username}</span>
                <span className="ownwr-comment">{newComment}</span>
                <div className="delete-container">
                    <DeleteOutlineOutlinedIcon className='comment-delete-icon' />
                </div>
                </div> : null
            }   

          {comments &&
            comments.map((c, index) => {
              return (
                <FriComment
                  comment={c.comment}
                  avatar={c.user.avatar.url}
                  username={c.user.username}
                  id={c.user._id}
                  key={index}
                  postOwner={postOwner}
                  postId={postId}
                />
              );
            })}
        </div>
      </div>
    </>
  );
};

export default Comment;
