import { Avatar } from '@mui/material'
import React from 'react'
import './user.css'

const User = ({avatar, userName, userId}) => {
  return (
    <>
        <div className="user" id={userId}>
            <Avatar src={avatar} className="user-avatar"/>
            <span className='user-name'>
                {userName}
            </span>
        </div>
    </>
  )
}

export default User