import './App.css';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom'
import Login from './Components/Login/Login';
import Signin from './Components/Signin/Signin';
import {useDispatch, useSelector} from "react-redux"
import { useEffect, useState } from 'react';
import { loadUser } from './Actions/User';
import Home from './Components/Home/Home';
import Footer from './Components/Footer/Footer';
import LikedBy from './Components/Liked_by/LikedBy';
import NewPost3 from './Components/NewPost3/NewPost3';
import MyProfile from './Components/MyProfile/MyProfile';
import Explore from './Components/Explore/Explore';
import SeUser from './Components/Explore/Search/SearchUser/SeUser';
import FriProfile from './Components/FriendProfile/FriProfile';
import MyFollowers from './Components/MyFollowers/MyFollowers';
import MyFollowing from './Components/MyFollowing/MyFollowing';
import FriFollowers from './Components/FriendProfile/FriFollowers/FriFollowers';
import FriFollowings from './Components/FriendProfile/FriFollowings/FriFollowings';
import Messages from './Components/Messages/Messages';
import LogOut from './Components/LogOut';
import Loder from './Components/Loder/Loder';
import Comment from './Components/Comment/Comment';
import UpdatePost from './Components/Popubs/UpdatePost/UpdatePost';
import EditeProfile from './Components/EditeProfile/EditeProfile';
import SinglePost from './Components/SinglePost/SinglePost';
import GifLoader from './Components/Loder/GifLoader';
import LoadingBar from 'react-top-loading-bar'
import BigGifLoader from './Components/Loder/BigGifLoader';
import LandingPage from './Components/LandingPage/LandingPage';

function App({history}) {

  const dispatch = useDispatch();
  const {isAuthor} = useSelector((state) => state.user)
  const [progress, setProgress] = useState(25)

  useEffect(()=>{
    const fetchData = async () =>{
      await dispatch(loadUser())
    }
    fetchData();
  }, [dispatch])

  const topLoader = (value)=>{
    return setProgress(value)
  }

  return (
    <Router >
      <div className="App">
      <LoadingBar
        color='#c79be5'
        progress={progress}
        height={3}
        loaderSpeed={360}
        onLoaderFinished={() => setProgress(0)}
      />
        <Routes >
          <Route path='/' element={ isAuthor ? <Home  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/signin' element={isAuthor ? <Home  topLoader={topLoader}/> : <Signin />} />
          <Route path='/post/likes/:userId' element={ isAuthor ? <LikedBy  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/post/:comp/:postId' element={ isAuthor ? <SinglePost  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/newpost' element={ isAuthor ? <NewPost3  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/message' element={ isAuthor ? <Messages  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/explore' element={ isAuthor ? <Explore  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/search' element={ isAuthor ?<SeUser  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/account' element={isAuthor ? <MyProfile  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/friend/profile/:id' element={ isAuthor ? <FriProfile  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/me/followers' element={ isAuthor ? <MyFollowers  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/me/followings' element={ isAuthor ? <MyFollowing  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/user/followers/:id' element={ isAuthor ? <FriFollowers  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/user/followings/:id' element={ isAuthor ? <FriFollowings topLoader={topLoader} /> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/logout' element={ <LogOut topLoader={topLoader}/> } />
          <Route path='/loader' element={ isAuthor ? <Loder  topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/post/comment/:comp/:postId' element={ isAuthor ? <Comment topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/post/update/:postId' element={ isAuthor ? <UpdatePost topLoader={topLoader}/>  : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/account/edit' element={ isAuthor ? <EditeProfile topLoader={topLoader}/> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
          <Route path='/test' element={ isAuthor ? <LandingPage /> : <Login isAuthor={isAuthor} topLoader={topLoader}/>} />
        </Routes>
        {
        // isAuthor && <Footer /> 
          isAuthor !== null && isAuthor === true ? <Footer />: null
        }
      </div>

    
    </Router>
  );
}

export default App;
