import {configureStore } from "@reduxjs/toolkit"
import { allCommentReducer, deleteCommentReducer, deletePostReducer, explorePost, folloingUserPost, likeUnlikeReducer, newPostReducer, postAllLikesReducer, pushCommentReducer, singlePost, updatePostReducer} from "./Redusers/PostReduser";
import { avatarUpdateReducer, followUnfollowReducer, FriFollowersReducer, FriFollowingsReducer, friProfileReducer, myFollowerReducer, myFollowingrReducer, myProfileReducer, searchUserReducer, SuggetionsReducer, usernameOrEmailReducer, userReduser } from "./Redusers/UserReduser";

const store = configureStore({
    reducer: {
        user: userReduser,
        followingPost: folloingUserPost,
        likeUnlike: likeUnlikeReducer,
        postAllLikes: postAllLikesReducer,
        newPost: newPostReducer,
        myProfile: myProfileReducer,
        searchUser: searchUserReducer,
        friProfile: friProfileReducer,
        followUnfollow: followUnfollowReducer,
        myFollowers: myFollowerReducer,
        myFollowings: myFollowingrReducer,
        friFollowers: FriFollowersReducer,
        friFollowings: FriFollowingsReducer,
        suggestions: SuggetionsReducer,
        postAllComments: allCommentReducer,
        pushComment: pushCommentReducer,
        dleteComment: deleteCommentReducer,
        deletePost: deletePostReducer,
        postUpdate: updatePostReducer,
        usernameOrEmailUpdate: usernameOrEmailReducer,
        avatarUpdate: avatarUpdateReducer,
        singlePost: singlePost,
        explorePost: explorePost
    }
});
  
export default store;