import {createReducer} from "@reduxjs/toolkit"

const initialState = {
    isAuthor: false
}

export const userReduser = createReducer(initialState, {
    LoginRequest: (state, action) => {
        state.loading = true
    },
    LoginSuccess: (state, action) => {
        state.loading = false
        state.user = action.payload
        state.isAuthor = true
    },
    LoginFailuer: (state, action) => {
        state.loading = false
        state.error = action.payload
        state.isAuthor = false
        state.message = action.message
    },

    SigninRequest: (state, action) => {
        state.loading = true
    },
    SigninSuccess: (state, action) => {
        state.loading = false
        state.user = action.payload
        state.isAuthor = true
    },
    SigninFailuer: (state, action) => {
        state.loading = false
        state.error = action.payload
        state.isAuthor = false
        state.message = action.message
    },

    LodingRequest: (state, action) => {
        state.isAuthor = null
        state.loading = true
    },
    LodingSuccess: (state, action) => {
        state.loading = false
        state.user = action.payload
        state.isAuthor = true
    },
    LodingFailuer: (state, action) => {
        state.loading = false
        state.error = action.payload
        state.isAuthor = false
    },

    LogoutRequest: (state, action) => {
        state.loading = true
    },
    LogoutSuccess: (state, action) => {
        state.loading = false
        state.isAuthor = false
    },
    LogoutFailuer: (state, action) => {
        state.loading = false
        state.error = action.payload
        state.isAuthor = false
    },

})

export const myProfileReducer = createReducer(initialState, {
    myProfileRequest: (state) =>{
        state.loading = true
        state.isAuthor = false
    },

    myProfileSuccess: (state, action) =>{
        state.loading = false
        state.myProfile = action.payload
        state.isAuthor = true
    },
    myProfileFailuer: (state, action) =>{
        state.loading = false
        state.error = action.payload
        state.isAuthor = false
    },
})

export const searchUserReducer = createReducer(initialState, {
    searchUserRequest: (state) =>{
        state.loading = true
    },
    searchUserSuccess: (state, action) =>{
        state.loading = false
        state.users = action.payload
    },
    searchUserFailuer: (state, action) =>{
        state.loading = false
        state.error = action.payload
        state.users = []
    },
})

