import React from 'react'
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import { Avatar } from '@mui/material';
import { NavLink, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { deleteCommnet, getAllComments, updateExpPostComDel, updateFirPostComDel, updateFollowingPostComDel } from '../../../Actions/Post';

const FriComment = ({comment, avatar, username, id, postOwner, postId}) => {

    const ownerId = useSelector(state => state.user.user._id)
    const dispatch = useDispatch();
    const params = useParams();
    const {post} = useSelector(state => state.followingPost)
    const expPosts = useSelector((state) => state.explorePost.posts);
    const singPost = useSelector(state => state.singlePost.post)

    const deleteCommenthendler = async() =>{
        const userId = localStorage.getItem('user._id')
        await dispatch(deleteCommnet(params.postId))
        await dispatch(getAllComments(params.postId))
        if(params.comp === "followUser"){
            dispatch(updateFollowingPostComDel(post,postId,userId))
          }
          else if(params.comp === "sugPosts"){
            dispatch(updateExpPostComDel(expPosts,postId,userId ))
        }
        else if(params.comp === "singPost"){
            dispatch(updateFirPostComDel(singPost,postId,userId))
        }
    }   

  return (
    
    <>
    {
        ownerId && 
         <div className="post-owner">
                <Avatar src={avatar} className='ownwr-img' />
                <NavLink to={`/friend/profile/${id}`} >
                    <span className="post-ownwr-id">
                    {username}
                    </span>
                </NavLink>
                <span className="ownwr-comment">
                    {comment}
                </span>

                {
                <div className="delete-container" onClick={deleteCommenthendler}>
                    {
                        ownerId === id  || postOwner === ownerId ? 
                            <DeleteOutlineOutlinedIcon className='comment-delete-icon' />
                        : null
                    }
                </div>
                }     
            </div>
    }
    </>
  )
}

export default FriComment