import React from "react";
import { Avatar, Typography } from "@mui/material";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import FavoriteIcon from "@mui/icons-material/Favorite";
import MapsUgcRoundedIcon from "@mui/icons-material/MapsUgcRounded";
import "./Post.css";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {  likeUnlike, updateExpPost, updateFollowingPost, updateFriPost } from "../../../Actions/Post";
import { useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import Model from "../../Model/Model";
import { LazyLoadImage } from "react-lazy-load-image-component";

const Post = ({
  ownerId,
  postId,
  caption,
  postImage,
  likes,
  comments,
  ownerImage,
  ownerName,
  postHeight,
  comp
}) => {

  const dispatch = useDispatch();
  const params = useParams();
  const [isLike, setIsLike] = useState(false);
  const [noOfLike, setNoOfLike] = useState(likes.length);
  const [isPostLoad, setIsPostLoad] = useState(false);
  const [showModel, setShowModel] = useState(false);

  const posts =  useSelector(state => state.followingPost.post)
  const expPosts = useSelector((state) => state.explorePost.posts);
  const friPost = useSelector(state => state.singlePost.post)

  useEffect(
    () =>
      async function () {
        const userId = await localStorage.getItem("user._id");
        if (userId !== undefined) {
          for (let i = 0; i < likes.length; i++) {
            if (likes[i].toString() === userId.toString()) {
              setIsLike(true);
              break;
            } else {
              setIsLike(false);
            }
          }
        }
    },[postId]);

  
  const likeHandler = async() => {
    setIsLike(!isLike);
    !isLike ? setNoOfLike(noOfLike + 1) : setNoOfLike(noOfLike - 1);
    const userId = await localStorage.getItem("user._id");
    if(comp === "sugPosts" ){
      await dispatch(updateExpPost(expPosts, postId, userId))
    }
    else if(comp === "followUser"){
      await dispatch(updateFollowingPost(posts, postId, userId))
    }
    else if(params.comp === "singPost"){
      await dispatch(updateFriPost(friPost,postId,userId))
    }
    await dispatch(likeUnlike(postId));
   
  };

  const closeModel = () => {
    if (showModel) {
      setShowModel(false);
    }
  };

  return (
    <>
      <div className={`post-post`}>
        {showModel && (
          <Model
            className="model-post"
            closeModel={closeModel}
            postId={postId}
            ownerId={ownerId}
          />
        )}

        <div className="post-header" onClick={closeModel}>
          <Avatar src={ownerImage} className="post-owner-img" />
          <NavLink to={`/friend/profile/${ownerId}`}>
            <Typography className="post-owner-name">{ownerName}</Typography>
          </NavLink>
          <div className="post-three-dot" onClick={() => setShowModel(true)}>
            <MoreHorizIcon />
          </div>
        </div>

        <div className="post-image" style={{height: postHeight == 1080   ? '365px': '451px'}}  onClick={closeModel}>
          {
            // <img src={postImage} alt="" className='img'/>
            <LazyLoadImage
                src={postImage}
                effect='blur'
                width="100%"
                height="100%"
                color='blue'
            >
             </LazyLoadImage>
          }
        </div>

        <div className="post-detail2" onClick={closeModel}>
          <div className="post-icons">
            {isLike === true ?
            (
              <FavoriteIcon onClick={likeHandler} />
            ) : (
              <FavoriteBorderIcon onClick={likeHandler} />
            )}
            <NavLink to={`/post/comment/${comp}/${postId}`}>
              <MapsUgcRoundedIcon />
            </NavLink>
          </div>

          <NavLink
            className="total-post-likes"
            id={postId}
            to={`/post/likes/${postId}`}
          >
            {noOfLike} likes
          </NavLink>

          <div className="post-owner">
            <Typography className="post-owner-name">
              {ownerName} <span className="post-caption">{caption}</span>
            </Typography>
          </div>

          <NavLink to={`/post/comment/${comp}/${postId}`}>
            <div className="total-post-comments">
              View all {comments.length} comments
            </div>
          </NavLink>
        </div>
      </div>
    </>
  );
};

export default Post;
