import React, { useState } from "react";
import Search from "./Search/Search";
import { NavLink } from "react-router-dom";
import "./explore.css";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { getExplorePosts } from "../../Actions/Post";
import InfiniteScroll from "react-infinite-scroll-component";
import Loder from "../Loder/Loder";

const Explore = ({topLoader}) => {
  const dispatch = useDispatch();
  const posts = useSelector((state) => state.explorePost);
  const [post, setPost] = useState([]);
  let skip = 0;

  useEffect(() => {
    const fetcData = async () => {
      topLoader(45)
      if (!posts.posts) {
        await dispatch(getExplorePosts(0));
      }
      topLoader(100)
    };
    fetcData();
  }, []);

  useEffect(() => {
    if (post.length === 0 && posts.posts !== undefined) {
      setPost(posts.posts);
    }
  }, [posts]);

  const fetchMoreData = async () =>{
    skip += 20;
    await dispatch(getExplorePosts(skip));
  }

  return (
    <div className="explore">
      <NavLink to="/search">
        <Search />
      </NavLink>

        {/* <InfiniteScroll
          dataLength={post.length}
          next={fetchMoreData}
          // inverse={true}
          className="all-explore-posts"
          hasMore={true}
          loader={<Loder />}
        >
            {post &&
            post.length > 0 &&
            post.map((p) => (
              <NavLink to={`/post/${p._id}`} className="explore-image-div">
                <img src={p.image.public_url} alt="" className="img-explore" />
              </NavLink>
            ))}
        </InfiniteScroll> */}

      
      
    </div>
  );
};

export default Explore;
