import React, { useCallback, useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import ClearIcon from '@mui/icons-material/Clear';
import './editProfile.css'
import { Avatar } from '@mui/material';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getMyProfile, loadUser, updateAvatar, updateUserNameEmail } from '../../Actions/User';
import Loder  from '../Loder/Loder'

import Cropper from 'react-easy-crop'
import getCroppedImg from '../NewPost3/cropImage'
import {Button, Slider, Typography }from '@mui/material'

const EditeProfile = ({topLoader}) => {

    const [crop, setCrop] = useState({ x: 0, y: 0 })
    const [rotation, setRotation] = useState(0)
    const [zoom, setZoom] = useState(1)
    const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
    const [croppedImage, setCroppedImage] = useState(null)
    const [accept, setAccept] = useState(1/1)

    const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
        setCroppedAreaPixels(croppedAreaPixels)
      }, [])
    
      const showCroppedImage = useCallback(async () => {
        
        try {
          topLoader(45)
          const croppedImage = await getCroppedImg(
            image,
            croppedAreaPixels,
            rotation
          )
          setCroppedImage(croppedImage)
        } catch (e) {
          console.error(e)
        }
        topLoader(100)
        setImage(null)
      }, [croppedAreaPixels, rotation])


    const navigate = useNavigate();
    const inputRef = useRef()  
    const dispatch = useDispatch();

    const user = useSelector(state => state.user.user)

    const [image, setImage] = useState(null)
    const [avatar, setAvatar] = useState(null)
    const [username, setUsername] = useState("")
    const [tempUsername, setTempUsername] = useState("")
    const [email, setEmail] = useState("")
    const [tempEmail, setTempEmail] = useState("")
    const [isLoader, setIsLoader] = useState(false)

    const triggerImagePopup = () =>{
        inputRef.current.click();
    }

    useEffect(()=>{
        const fetchData = async () =>{
            if(!user){
              topLoader(45)
              setIsLoader(true)
              await dispatch(loadUser())
              topLoader(100)
              setIsLoader(false)
            }
          }    
          fetchData();
          setAvatar(user.avatar.url)
          setUsername(user.username)
          setTempUsername(user.username)
          setEmail(user.email)
          setTempEmail(user.email)
    }, [])

    
  const imageHandler = (e) =>{
    const file = e.target.files[0]
    const Reader = new FileReader()
    Reader.readAsDataURL(file)

    Reader.onload = ()=>{
        if(Reader.readyState === 2){
            setImage(Reader.result)
        }
    }
}

  const editSubmitHendlar = async() =>{
    if(((username !== tempUsername)  || (email !== tempEmail)) && (croppedImage !== null)){
      setIsLoader(true)
      topLoader(45)
      await dispatch(updateAvatar(croppedImage))
      await dispatch(updateUserNameEmail(tempUsername, tempEmail))
      topLoader(100)
      setIsLoader(false)
    }
    else if(croppedImage !== null){
      setIsLoader(true)
      topLoader(45)
      await dispatch(updateAvatar(croppedImage))
      topLoader(100)
      setIsLoader(false)
    }
    else if((username !== tempUsername) || (email !== tempEmail)){
      topLoader(45)
      setIsLoader(true)
      await dispatch(updateUserNameEmail(tempUsername, tempEmail))
      setIsLoader(false)
      topLoader(100)
    } else {
      console.log('no change')
    }
    window.location = '/account'
  }

  return (


    <>

    <>
      {
        isLoader === true ? <Loder /> :  
    
    <>
     {   
        image !== null ?
        <>
        <Cropper
            image={image}
            crop={crop}
            rotation={rotation}
            zoom={zoom}
            aspect={accept}
            onCropChange={setCrop}
            onRotationChange={setRotation}
            onCropComplete={onCropComplete}
            onZoomChange={setZoom}
            />
            <Button
            onClick={showCroppedImage}
            variant="contained"
            color="primary"
            >
            Next
            </Button>
        </> : 

          <>

        <div className="editeProfile-container">
             <div className="likeby-header">
                <div className="back-likedby" onClick={()=> navigate(-1)}>
                    <ClearIcon className='close-likedBy'/>
                </div>
                <div className="likedBy-head">
                    Edit profile
                </div>
            </div>
            <div className="main-edite-container">
                <div className="avatar-edit-container">
                    <div className="avatar-img">
                        <Avatar src={croppedImage !== null ? croppedImage :  avatar} sx={{ width: 38, height: 38 }} />
                    </div>
                    <div className="avatar-input">
                        <span className="username-avatar-name">
                            {user.username}
                        </span>
                        <span className="avatar-input-ref" onClick={triggerImagePopup}>
                            Change profile photo
                        </span>
                        <input type="file" accept='image/*' onChange={imageHandler} id="fileBtn" placeholder='Add Avatar' className="input" ref={inputRef} style={{display: "none"}} />
                    </div>
                </div>
                <div className="text-inputs">
                  <div className="username-input-contralls">
                    <h4>Username</h4>
                    <div className="userinput">
                      <input type="text" value={tempUsername} onChange={(e)=> setTempUsername(e.target.value)}/>
                    </div>
                  </div>
                  <div className="email-input-contralls">
                    <h4>Email</h4>
                    <div className="emailinput">
                      <input type="text" value={tempEmail}  onChange={(e)=> setTempEmail(e.target.value)}/>
                    </div>
                  </div>
                  <div className="edit-submit-btn">
                    <button onClick={editSubmitHendlar}>Submit</button>
                  </div>
                </div>
            </div>
        </div>
        </>
        }
       </>
         }
      </>
    </>
  )
}

export default EditeProfile