import React, { useState } from 'react'
import { useEffect } from 'react'
import "./home.css"
import Post from './Post/Post'
import {useDispatch, useSelector} from "react-redux"
import { getAllPost, getExploreMorePost, getExplorePosts } from '../../Actions/Post'
import Suggestion from '../Suggestion/Suggestion'
import { getSuggestions, loadUser } from '../../Actions/User'
import InfiniteScroll from "react-infinite-scroll-component";
import GifLoader from '../Loder/GifLoader'
import BigGifLoader from '../Loder/BigGifLoader'
import { Avatar } from '@mui/material'


const Home = ({topLoader}) => {

  const dispatch = useDispatch()
  const user = useSelector(state => state.user.user)
  const post = useSelector(state => state.followingPost.post)
  const following = useSelector(state => state.user.user.following)
  const suggestion = useSelector(state => state.suggestions.suggetion)
  const expPosts = useSelector((state) => state.explorePost);
  const [isLoader, setIsLoader] = useState(false)

  let skip = 0;
  useEffect(()=> {
    if(!post)
    {
        const fetchData = async () =>{
          topLoader(40)
          setIsLoader(true)
          await dispatch(getAllPost());
          await dispatch(getSuggestions());
          setIsLoader(false)
        } 
        fetchData()
      }
      topLoader(82)

      if(!expPosts.posts){
        dispatch(getExplorePosts(skip))
        console.log("posttsss")
      }
      topLoader(100)
  }, [])

  const fetchMoreData = async () =>{
    skip = expPosts.posts.length;
    dispatch(getExploreMorePost(expPosts.posts,skip))
  }
  
  return (
    <div style={{backgroundColor: "#000000", minHeight: "100vh"}}>
    {
      isLoader === true ? <BigGifLoader /> :
      <div className="home">
      <div className="header-home">
          <img src="../pics/logo.png"  alt="" />
          <h2>Snappy</h2>
        </div>
        {
        following.length !== 0 ? 
        <div className="story-home">
          {/* to do */}
          {
            // user &&  
            //   <>
            //     <div className="myStory">
            //       <Avatar src={user.avatar.url} sx={{ width: 55, height: 55 }} className='avatar-story' />
            //     <h5>{user.username}</h5>
            //     </div>
            //   </>
          }
        </div>
         : null
        }
        <div className="post-home">
                   
          {
            post && post[0] !== null ? 
              post.map((post, index)=> ( <Post 
                  key={post._id}
                  caption={post.caption}
                  postImage={post.image.public_url}
                  likes={post.likes}
                  comments={post.comments}
                  ownerImage={post.owner.avatar.url}
                  ownerName={post.owner.username}
                  ownerId={post.owner._id}
                  postId={post._id}
                  postHeight={post.postHeight}
                  comp="followUser"
                /> 
              )):null
             }

        </div>

        {
            suggestion && suggestion.length >= 2 ? <>
            <div className='suggestion-home'>
              {
                suggestion && suggestion.length >= 1 ?
                suggestion.map((user, index) =>{
                  return (
                    <Suggestion 
                      avatar={user.avatar.url}
                      username={user.username}
                      userId={user._id}
                      key={index}
                    />
                  )
                }): null
              }
            </div>
            </>:null
        } 

        <h4 className='sugg-post-text'>Suggested Posts</h4>
        {
          expPosts && expPosts.posts !== undefined ?
           <InfiniteScroll
           dataLength={expPosts.posts.length}
           next={fetchMoreData}
           // inverse={true}
           className="post-home"
           hasMore={true}
           loader={<GifLoader />}
           >
             {
              expPosts && expPosts.posts !== undefined  ?
                expPosts.posts.map((post, index)=> ( <Post 
                  key={post._id}
                  caption={post.caption}
                  postImage={post.image.public_url}
                  likes={post.likes}
                  comments={post.comments}
                  ownerImage={post.owner.avatar.url}
                  ownerName={post.owner.username}
                  ownerId={post.owner._id}
                  postId={post._id}
                  postHeight={post.postHeight}
                  comp="sugPosts"
                /> 
              )):null
             }
             
           </InfiniteScroll>:null
        }

      </div>
}    </div>
  )
}

export default Home