import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { useSelector } from 'react-redux'
import {NavLink} from 'react-router-dom'
import User from '../../../Home/Users/User'
import Search from '../Search'
import './seUser.css'

const SeUser = () => {

  const {users} = useSelector(state => state.searchUser)

  // const [seUser, setSeUser] = useState(null)

  // useEffect(()=>{
  //   setSeUser(users)
  // }, [users])

  return (
    <div className='se-user'>
        <Search />
        <div className="search-users">
          {
            users && 
            users.map((user)=>(
              <NavLink to={`/friend/profile/${user._id}`} className='links'>
                <User 
                  key={user._id}
                  avatar={user.avatar.url}
                  userName={user.username}
                  userId={user._id}
                /> 
              </NavLink>
            ))
          }
        </div>
    </div>
  )
}

export default SeUser