import React, { useEffect } from 'react'
import { useDispatch, useSelector} from 'react-redux';
import {useParams, useNavigate, NavLink, useHistory  } from 'react-router-dom';
import { getAllLike } from '../../Actions/Post';
import ClearIcon from '@mui/icons-material/Clear';
import "./likedby.css"
import { useState } from 'react';
import User from '../Home/Users/User';

const LikedBy = ({topLoader}) => {

    const params = useParams();
    const dispatch = useDispatch()
    const likes = useSelector(state => state.postAllLikes.like)
    const [users, setUsers] = useState(null)
    const navigate = useNavigate();

    useEffect(()=>{
        topLoader(40)
        const fetchData = async() =>{
            await dispatch(getAllLike(params.userId))
            topLoader(100)
        }
        fetchData();
    }, [dispatch])

    useEffect(()=>{
        likes !== undefined ?
            setUsers(likes.likes):
            setUsers(null)
    }, [likes])

  return (
    <div className='likedby'>
        <div className="likeby-header">
            <div className="back-likedby" onClick={()=> navigate(-1, {replace: false})}>
                <ClearIcon className='close-likedBy'/>
            </div>
            <div className="likedBy-head">
               Likes
            </div>
        </div>
        <div className='all-liked-user'>
            {
                users !== null ?
                   users.map((user, index)=>(
                    <NavLink to={`/friend/profile/${user._id}`} className='links'>
                    <User 
                    key={user._id}
                    avatar={user.avatar.url}
                    userName={user.username}
                    userId={user._id}
                    /> 
                    </NavLink>
                   )) 
                : null
            }     
        </div>
    </div>
  )
}

export default LikedBy