import axios from "axios";
import cloneDeep from "lodash.clonedeep";

export const getAllPost = ()=> async (dispatch)=>{
    try {
        
        dispatch({
            type: "folloingPostRequest"
        })

        const {data} = await axios.get('/api/v1/users/posts');

        dispatch({
            type: "folloingPostSuccess",
            payload: data.post
        })

    } catch (error) {
        dispatch({
            type: "folloingPostFailuer",
            payload: error
        })
    }
}

export const getSinglePost = (postId)=> async (dispatch)=>{
    try {
        
        dispatch({
            type: "singlePostRequest"
        })

        const {data} = await axios.get(`/api/v1/post/${postId}`);

        dispatch({
            type: "singlePostSuccess",
            payload: data.post
        })

    } catch (error) {
        dispatch({
            type: "singlePostFailuer",
            payload: error
        })
    }
}

export const updateFriPost = (posts, postId, userId) => async (dispatch) => {
    try {
        console.log(posts)
        var flag = -1;
        const newArr = cloneDeep(posts)

            for(let i=0; i<newArr.likes.length; i++){
                if(newArr.likes[i] === userId){
                    flag = i;
                    break;
                } else {
                    flag = -1;
                }
            }

            if(flag !== -1){
                newArr.likes.splice(flag, 1)
                dispatch({
                    type: "singlePostUpdateSuccess",
                    payload: newArr
                })
            } 
            else {
                newArr.likes.unshift(userId)
                dispatch({
                    type: "singlePostUpdateSuccess",
                    payload: newArr
                })
            }
    } catch (error) {
        console.log(error)
    }
}


export const updateFirPostCom = (posts, postId, userId,comment) => async (dispatch) => {
    try {

        var flag = -1;
        const newArr = cloneDeep(posts)

            for(let i=0; i<newArr.comments.length; i++){
                if(newArr.comments[i].user === userId){
                    flag = i;
                    break;
                }
                else{
                    flag = -1;
                }
            }

        if(flag !== -1){
            newArr.comments[flag].comment = comment
        }
        else{
            const commObj = {
                user: userId,
                comment: comment,
                _id: userId
            }
            newArr.comments.splice(flag,1)
            newArr.comments.push(commObj)
        }

        dispatch({
            type: "singlePostUpdateCom",
            payload: newArr
        })

    } catch (error) {
        console.log(error)
    }
}

