export const updateFollowingPost = (posts, postId, userId) => async (dispatch) => {
    try {

        var flag = -1;
        var postIndex = -1;
        const newArr = cloneDeep(posts)

        for(let i=0; i<newArr.length; i++){
            if(newArr[i]._id === postId){
                flag = i;
                break;
            } else {
                flag = -1;
            }
        }
        
        if(flag !== -1){
            postIndex = flag;
            flag = -1;
            for(let i=0; i<newArr[postIndex].likes.length; i++){
                if(newArr[postIndex].likes[i] === userId){
                    flag = i;
                    break;
                } else {
                    flag = -1;
                }
            }

            if(flag !== -1){
                newArr[postIndex].likes.splice(flag, 1)
                dispatch({
                    type: "folloingPostUpdateSuccess",
                    payload: newArr
                })
            } 
            else {
                newArr[postIndex].likes.unshift(userId)
                dispatch({
                    type: "folloingPostUpdateSuccess",
                    payload: newArr
                })
            }
            console.log('heyyy')
        } else {
                return;
        }
       
    } catch (error) {
        console.log(error)
    }
}

