export const getExploreMorePost = (posts,skip) => async (dispatch) => {
    try {
        const newArr = cloneDeep(posts)
        const {data} = await axios.get(`/api/v1/explore/posts/${skip}`)
        const newData = [...newArr,...data.posts]

        dispatch({
            type: "explorePostAddMore",
            payload: newData
        })

    } catch (error) {
        console.log(error)
        dispatch({
            type: "explorePostFailuer",
            payload: error.message
        })
    }
}

