import React from "react";
import "./login.css"
import {NavLink} from "react-router-dom"
import { useState } from "react";
import {useDispatch, useSelector} from 'react-redux'
import { loginUser } from "../../Actions/User";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";
import { useEffect } from "react";
import BigGifLoader from "../Loder/BigGifLoader";
import LandingPage from "../LandingPage/LandingPage";

const Login = ({isAuthor, topLoader}) => {

  const [username, setUserName]  = useState("");
  const [password, setPassWord]  = useState("");
  const dispatch = useDispatch()

  const message = useSelector(state => state.user.message)
  const [isLoader, setIsLoader] = useState(false)

  useEffect(()=>{
    topLoader(100)
    if(message === 'user not found' || message === 'pass wrong'){
      toast.error("Invalid details", toastObj);
    }
  }, [message])

  const LoginSubmit = async(e) =>{
    e.preventDefault()
    console.log("click")
    setIsLoader(true)
    topLoader(45)
    await dispatch(loginUser(username, password))
    setIsLoader(false)
    if(message === 'user not found' || message === 'pass wrong'){
      toast.error("Invalid details", toastObj);
    }
    topLoader(100)
  } 
  
  const toastObj = {
    position: "bottom-right",
    autoClose: 8000,
    pauseOnHover: true,
    draggable: true,
    theme: "dark",
  };
  
  return (
    <>
    {

  isAuthor === null ? <LandingPage /> :

         isLoader === true ? <BigGifLoader /> :
    
      <div className="container1">
        <form className="form-login" onSubmit={LoginSubmit}>
          <div className="header">
            <img src="../pics/logo.png" alt="" />
            <span className="app-name">
              <h1>SNAPPY</h1>
            </span>
          </div>
          <input
            type="text"
            name="username"
            className="input first-input"
            placeholder="Username"
            value={username}
            onChange={(e)=> setUserName(e.target.value)}
            required
          />
          <input
            type="password"
            name="password"
            className="input"
            placeholder="Password"
            value={password}
            onChange={(e)=> setPassWord(e.target.value)}
            required
          />
          <input
            type="submit"
            className="btn"
            value="Log in"
          />
          <div className="form-text">
            <span className="nor-text">Have an Account ?</span>
            <span className="acc-text">
              <NavLink className="login-link" to="/signin">
                Signin
              </NavLink>
            </span>
            <ToastContainer  />
          </div>
        </form>
      </div>
    }
    </>
  );
};

export default Login;
