import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom'
import { updatePost } from '../../../Actions/Post';
import { getMyProfile } from '../../../Actions/User';
import ClearIcon from '@mui/icons-material/Clear';
import './updatePost.css'
import BigGifLoader from '../../Loder/BigGifLoader';

const UpdatePost = ({topLoader}) => {

    const params = useParams();
    const navigate = useNavigate()
    const dispatch = useDispatch();

    const post = useSelector(state => state.myProfile.myProfile)
    console.log(post)

    const [isLoader, setIsLoader] = useState(false)
    const [capstion, setCapstion] = useState("")
    const [image, setImage] = useState("")

    useEffect(()=> async function(){
        topLoader(45)
        await dispatch(getMyProfile())
        topLoader(100)
    }, [])

    useEffect(()=>{

        if(post){
            for(let i=0; i<post.post.length; i++){
                if(post.post[i]._id === params.postId){
                    setImage(post.post[i].image.public_url)
                }
            }
        }
    }, [post])

    const updateCationHandler = async() =>{
        const id = params.postId;
        setIsLoader(true)
        await dispatch(updatePost(id, capstion))
        setIsLoader(false)
        navigate(-1)
    }

  return (
    <>
        {
            isLoader === true ? <BigGifLoader /> :
            <>
        <div className="likeby-header">
            <div className="back-likedby" onClick={()=> navigate(-1, {replace: false})}>
                <ClearIcon className='close-likedBy'/>
            </div>
            <div className="likedBy-head">
               Likes
            </div>
        </div>
        <div className="update-post-container">
            <div className='img-div'>
                    <img src={image} className="image"/>
            </div>
            <input type="text" placeholder='Caption' className='caption-input2' value={capstion} onChange={(e) => setCapstion(e.target.value)}/>
            <input type="button" className='caption-btn' value="Post" onClick={updateCationHandler}/>
        </div>
        </>
        }
    </>
  )
}

export default UpdatePost