import { Avatar } from '@mui/material'
import React from 'react'
import './friProfile.css'
import {useDispatch, useSelector} from 'react-redux'
import { useEffect } from 'react'
import { followUnfollow, friProfile } from '../../Actions/User'
import { useParams, useNavigate, NavLink }  from "react-router-dom"
import ClearIcon from '@mui/icons-material/Clear';
import { useState } from 'react'
import Post from '../Home/Post/Post'
import Loder from '../Loder/Loder'
import BigGifLoader from '../Loder/BigGifLoader'


const FriProfile = ({topLoader}) => {

  let { id } = useParams();
  const dispatch = useDispatch()
  const profile = useSelector(state => state.friProfile.users)
  const follow = useSelector(state => state.friProfile.follow)
  const message = useSelector(state => state.followUnfollow.message)
  const navigate = useNavigate();
  const [ownerId, setOwnerId] = useState();
  const [isLoader, setIsLoader] = useState(false)

  const [foLength, setFoLength] = useState(null)
  const [fomessage, setFoMessage] = useState("")
  useEffect(()=>{
    const fetchData = async() =>{
      topLoader(45)
      setIsLoader(true)
      setOwnerId(localStorage.getItem('user._id'));
      await dispatch(friProfile(id))
      setIsLoader(false)
      await dispatch({type: "singlePostClear"})
      topLoader(100)
    }
    fetchData()
  }, [id])

  useEffect(()=>{
    if(profile){
      setFoLength(profile.followors.length)
      if(follow){
        setFoMessage("following")
      } else {
        setFoMessage("follow")
      }
    }
  }, [profile])

  useEffect(()=>{
    if(message){
      setFoMessage(message)
    }
  }, [message])

  const followUnfollowHandel = async() =>{
    setIsLoader(true)
    await dispatch(followUnfollow(id))
    setIsLoader(false)
  }

  useEffect(()=>{
    if(message === 'User follow'){
      setFoLength(foLength + 1)
      setFoMessage("following")
    } else if(message === 'User Unfollow') {
      setFoLength(foLength - 1)
      setFoMessage("follow")
    }
  }, [message])

  return (
   
    isLoader === true ? <BigGifLoader /> :
   
   <div style={{backgroundColor: "#000000", minHeight: "100vh"}}>   
      {
        profile ?
        <>
          <div className="likeby-header">
            <div className="back-likedby" onClick={() => navigate(-1)}>
                <ClearIcon className='close-likedBy'/>
            </div>
            <div className="likedBy-head">
               {profile.username}
            </div>
        </div>
         <div className="profile-detail">
            <div className="left-deatile">
                <Avatar className='avatar-detail'sx={{ width: 62, height: 62 }} src={profile.avatar.url}/>
                <h5> {profile.username} </h5>
            </div>
            <div className="right-detail">
              <p>{profile.username} </p>
              {
              id && ownerId !== id ? 
              <div>
                <button className='edit-profile2' onClick={followUnfollowHandel}>{fomessage} </button>
                <NavLink to='/message'>
                  <button className='edit-profile2'>message</button>
                </NavLink>
              </div> : 
               <div className="right-detail">
               {/* <p>{profile.username}</p> */}
               <button className='edit-profile'>Edit Profile</button>
             </div>
              }
            </div>
            <div className="follow-following">
              <div className="post-detail">
                <h3>{profile.post.length} </h3>
                <span>posts</span>
              </div>
              <div className="followers-detail">
                <NavLink to={`/user/followers/${profile._id}`} >
                  <h3>{foLength} </h3>
                </NavLink>
                <span>followers</span>
              </div>
              <div className="following-detail">
              <NavLink to={`/user/followings/${profile._id}`} >
                <h3>{profile.following.length}</h3>
              </NavLink>
                <span>following</span>
              </div>
            </div>
        </div> 
   
        <div className="all-explore-posts">
          {
            profile.post.map((p)=>(
                <NavLink to={`/post/singPost/${p._id}`} className='explore-image-div'>
                  <img src={p.image.public_url} alt="" className='img-explore'/>
                </NavLink>
            ))
          }
        </div>

      </>: null
      }
    </div>
  )
}

export default FriProfile