import React from 'react'
import "./footer.css"
import { NavLink, useNavigate} from 'react-router-dom'
import {
    Home,
    HomeOutlined,
    Add,
    AddOutlined,
    Search,
    SearchOutlined,
} from "@mui/icons-material"
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import { useState } from 'react'
import { Avatar } from '@mui/material';
import { useSelector } from 'react-redux';

const Footer = () => {

    const [tab, setTabe] = useState(window.location.pathname);
    const avatar = useSelector(state => state.user.user.avatar.url)



  return (
    <>
        <div className="footer">
            <div>
                <NavLink to="/" onClick={() => setTabe('/')}>
                    {tab === '/'? <Home /> : <HomeOutlined />}
                </NavLink>
            </div>

            <div>
                <NavLink to="/explore" onClick={()=> setTabe("/explore")}>
                {tab === '/explore'? <Search /> : <SearchOutlined />}
                </NavLink>
            </div>

            <div>
                <NavLink to="/newpost" onClick={()=> setTabe("/newpost")}>
                {tab === '/newpost'? <Add /> : <AddOutlined />}
                </NavLink>
            </div>

            <div>
                <NavLink to="/message" onClick={()=> setTabe("/message")}>
                {tab === '/message'?<ChatBubbleOutlineIcon /> : <ChatBubbleOutlineIcon />}
                </NavLink>
            </div>

            <div>
                <NavLink to="/account" onClick={()=> setTabe("/account")}>
                {tab === '/account'? <Avatar src={avatar} sx={{ width: 27, height: 27 }} className='profile-click profile-icon' /> : <Avatar src={avatar} sx={{ width: 25, height: 25 }} className='profile-icon' />}
                </NavLink>
            </div>
        </div>
    </>
  )
}

export default Footer