import { Avatar } from '@mui/material'
import React, { useEffect } from 'react'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { followUnfollow } from '../../Actions/User'
import Loder from '../Loder/Loder'
import './Suggetion.css'

const Suggestion = ({avatar, username, userId}) => {

  const [isFollow, setIsFollow] = useState(false)
  const dispatch = useDispatch();
  const [isLoader, setIsLoader] = useState(false)
  const [author, setAuthor] = useState(null)
  useEffect(()=>{
    setAuthor(localStorage.getItem("user._id"))
  }, [])

  const followHendler = async (e) =>{
    setIsLoader(true)
    await dispatch(followUnfollow(e.target.id))
    setIsLoader(false)
    setIsFollow(!isFollow)
  }

  return (
    <>
      {  
        isLoader === true ? <Loder /> :
        author !== null && author !== userId ?
      <div className="suggestion-card">
          <div className="avatar-suggestion">
            <Avatar src={avatar} className='img' sx={{ width: 120, height: 120 }}  />
          </div>
          <div className="username-suggestion">
            <NavLink to={`/friend/profile/${userId}`}>
              <p>{username}</p>
            </NavLink>
          </div>
          <div className="btn-suggestion">
            <button onClick={followHendler} id={userId}>
              {
                isFollow ? 'following' : 'follow'
              }
            </button>
          </div>
        </div> : null}
    </>
  )
}

export default Suggestion