import React, { useState } from 'react'
import ClearIcon from '@mui/icons-material/Clear';
import '../MyFollowers/myFollower.css'
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { getFollowings } from '../../Actions/User';
import User from '../Home/Users/User';
import {NavLink, useNavigate } from 'react-router-dom';
import BigGifLoader from '../Loder/BigGifLoader';

const MyFollowing = ({topLoader}) => {

    const dispatch = useDispatch();
    const followings = useSelector(state => state.myFollowings.followings)
    const navigate = useNavigate();
    const [isLoader, setIsLoader] = useState(false)

    useEffect(()=>{
        const fetchData = async () =>{
            setIsLoader(true)
            topLoader(45)
            await dispatch(getFollowings())
            topLoader(100)
            setIsLoader(false)
        }
        fetchData()
    }, [dispatch])

  return (
    <>
    {
        isLoader === true ? <BigGifLoader />:
        <>
        <div className="followers-conatiner">
        <div className="likeby-header">
            <div className="back-likedby" onClick={() => navigate(-1)}>
                <ClearIcon className='close-likedBy'/>
            </div>
            <div className="likedBy-head">
               Followers
            </div>
        </div>

        <div className='all-liked-user'>
            {
                followings &&
                   followings.map((user)=>(
                    <NavLink to={`/friend/profile/${user._id}`} className='links'>
                        <User
                            avatar={user.avatar.url}
                            userName={user.username}
                            userId={user._id}
                        /> 
                    </NavLink>
                   ))
            }     
        </div>
        </div>
    
        </>
    }
    </>
  )
}

export default MyFollowing