import React, { useState, useCallback, useRef } from 'react'
import ReactDOM from 'react-dom'
import Cropper from 'react-easy-crop'
import {Button, Slider, Typography }from '@mui/material'
import CropPortraitOutlinedIcon from '@mui/icons-material/CropPortraitOutlined';
import CropSquareOutlinedIcon from '@mui/icons-material/CropSquareOutlined';
import getCroppedImg from './cropImage'
import { styles } from './styles'
import './newPost3.css'
import { useDispatch } from 'react-redux';
import { createNewPost } from '../../Actions/Post';
import BigGifLoader from '../Loder/BigGifLoader';



const dogImg =
  '../pics/neha1.jpg'

// const Demo = ({ classes }) => {
const Demo = ({ topLoader }) => {

    const inputRef = useRef()
    const dispatch = useDispatch();

  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [rotation, setRotation] = useState(0)
  const [zoom, setZoom] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
  const [croppedImage, setCroppedImage] = useState(null)
  const [image, setImage] = useState(null)
  const [accept, setAccept] = useState(1/1)
  const [postHeight, setPostHeight] = useState(1080)
  const [caption, setCaption] = useState("")
  const [isLoader, setIsLoader] = useState(false)

  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels)
  }, [])

  const showCroppedImage = useCallback(async () => {
    console.log(croppedAreaPixels)
    try {
      topLoader(45)
      const croppedImage = await getCroppedImg(
        image,
        croppedAreaPixels,
        rotation
      )
      setCroppedImage(croppedImage)
      topLoader(100)
    } catch (e) {
      console.error(e)
    }
  }, [croppedAreaPixels, rotation])

  const onClose = useCallback(() => {
    setCroppedImage(null)
  }, [])

  const triggerImagePopup = () =>{
    inputRef.current.click();
  }

  const selectFile = (e) =>{
    if(e.target.files && e.target.files.length > 0){
      const reader = new FileReader();
      reader.readAsDataURL(e.target.files[0])
      reader.addEventListener('load', () =>{
        setImage(reader.result)
      })
    }
  }

  const setImageIcon = () =>{
    console.log("heyy")
    accept === 1 ? setAccept(4/5) : setAccept(1)
    accept === 1 ? setPostHeight(1350) : setPostHeight(1080)
  }

  const postHendlar = async() =>{
    setIsLoader(true)
    await dispatch(createNewPost(caption,croppedImage, postHeight ))
    setIsLoader(false)
    window.location = '/account'
  }


  return (
    <div style={{backgroundColor: "#000000"}}>
        {
            !image &&
            <div className='input-choice'>
                <input type="file" accept='image/*' onChange={selectFile} ref={inputRef} style={{display: "none"}} />
                <Button 
                variant='contained'
                onClick={triggerImagePopup}
            > Choose
            </Button>
          </div>
        }

      {
        (croppedImage === null) && (image !== null) ?
        <>
        <div className={styles.cropContainer} >
        <Cropper
          image={image}
          crop={crop}
          rotation={rotation}
          zoom={zoom}
          aspect={accept}
          onCropChange={setCrop}
          onRotationChange={setRotation}
          onCropComplete={onCropComplete}
          onZoomChange={setZoom}
        />
      </div>
      <div className={styles.controls}>
        <div className={styles.sliderContainer}>
          <Typography
            variant="overline"
            classes={{ root: styles.sliderLabel }}
          >
            Zoom
          </Typography>
          <Slider
            value={zoom}
            min={1}
            max={3}
            step={0.1}
            aria-labelledby="Zoom"
            classes={{ root: styles.slider }}
            onChange={(e, zoom) => setZoom(zoom)}
          />
        </div>
        <Button
          onClick={showCroppedImage}
          variant="contained"
          color="primary"
          classes={{ root: styles.cropButton }}>
          Next
        </Button>
        <Button onClick={setImageIcon}>
          <div className="setImage-icon" >
            {accept === 1 ? <CropPortraitOutlinedIcon /> : <CropSquareOutlinedIcon />}
          </div>  
        </Button>
      </div>
      </>: null
      }
      {
        croppedImage && (
          <>
            {
              isLoader === true ? <BigGifLoader /> :
              <div className="final-post-page" >
                <div className="crop-image">
                  <img src={croppedImage} alt="" className='abcd'/>
                </div>
                <div className="caption-input">
                  <input type="text" value={caption} onChange={(e)=> setCaption(e.target.value)} placeholder='caption'/>
                </div>
                  <button className='post-btn' onClick={postHendlar}>Post</button>
             </div>

          }

          </>
        )
      }
    </div>
  )
}


export default Demo

