import { Avatar } from '@mui/material'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import './profileDetail.css'
import { NavLink } from 'react-router-dom'

const ProfileDetail = () => {

  const profile = useSelector(state => state.myProfile.myProfile)

  return (
    <>
      {
        profile &&
        <div className="profile-detail">
            <div className="left-deatile">
                <Avatar className='avatar-detail'sx={{ width: 62, height: 62 }} src={profile.avatar.url}/>
                <h5> {profile.username} </h5>
            </div>
            <div className="right-detail">
              <p>{profile.username}</p>
              <NavLink to='/account/edit'>
              <button className='edit-profile'>Edit Profile</button>
              </NavLink>
            </div>
            <div className="follow-following">
              <div className="post-detail">
                <h3>{profile.post.length}</h3>
                <span>posts</span>
              </div>
              <div className="followers-detail">
                <NavLink to='/me/followers' >
                  <h3>{profile.followors.length}</h3>
                </NavLink>
                <span>followers</span>
              </div>
              <div className="following-detail">
              <NavLink to='/me/followings' >
                <h3>{profile.following.length}</h3>
              </NavLink>
                <span>following</span>
                
              </div>
            </div>
        </div>
    }
    </>
  )
}

export default ProfileDetail