import React, { useCallback, useEffect, useRef, useState } from "react";
import "./signin.css"
import {NavLink} from "react-router-dom"
import { useDispatch, useSelector } from "react-redux";
import { signinUser } from "../../Actions/User";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";
import { Avatar } from "@mui/material";
import Loder from "../Loder/Loder";
import AddPhotoAlternateIcon from '@mui/icons-material/AddPhotoAlternate';



import Cropper from 'react-easy-crop'
import getCroppedImg from '../NewPost3/cropImage'
import {Button }from '@mui/material'
import BigGifLoader from "../Loder/BigGifLoader";

const Login = () => {


  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [rotation, setRotation] = useState(0)
  const [zoom, setZoom] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
  const [croppedImage, setCroppedImage] = useState(null)

  const [image, setImage] = useState(null)

  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels)
  }, [image])

  const showCroppedImage = useCallback(async () => {
    
    try {
      const croppedImage = await getCroppedImg(
        image,
        croppedAreaPixels,
        rotation
      )
      setCroppedImage(croppedImage)
    } catch (e) {
      console.error(e)
    }
    setImage(null)
  }, [croppedAreaPixels, rotation])

  const inputRef = useRef()  

  const [username, setUserName]  = useState("");
  const [password, setPassWord]  = useState("");
  const [email, setEmail]  = useState("");
  const [isLoader, setIsLoader] = useState(false)
  const dispatch = useDispatch()

  const message = useSelector(state => state.user.message)

  const toastObj = {
    position: "bottom-right",
    autoClose: 8000,
    pauseOnHover: true,
    draggable: true,
    theme: "dark",
  };
  
  useEffect(()=>{
    if(message === 'User validation failed: password: password must be at least 6 characters'){
      toast.error('Password must be 8 characters', toastObj)
    } else if(message === 'User alrady exist') {
      toast.error('UserName not available', toastObj)
    }
  }, [message])

  const imageHandler = (e) =>{
    const file = e.target.files[0]
    const Reader = new FileReader()
    Reader.readAsDataURL(file)

    Reader.onload = ()=>{
        if(Reader.readyState === 2){
            setImage(Reader.result)
        }
    }

}

const SigninSubmit = async(e) =>{
  e.preventDefault();
  setIsLoader(true)
  await dispatch(signinUser(username, password, email, croppedImage))
  setIsLoader(false)
  if(message === 'User validation failed: password: password must be at least 6 characters'){
    toast.error('Password must be 6 characters', toastObj)
  } else if(message === 'User alrady exist') {
    toast.error('UserName not available', toastObj)
  }else{
    window.location.pathname = '/'
  }
  
}

const triggerImagePopup = () =>{
  setCroppedImage(null)
  inputRef.current.click();
}

  return (
    <>
    { 
      image !== null && croppedImage === null ?
    <>
         <Cropper
          image={image}
          crop={crop}
          rotation={rotation}
          zoom={zoom}
          aspect={1/1}
          onCropChange={setCrop}
          onRotationChange={setRotation}
          onCropComplete={onCropComplete}
          onZoomChange={setZoom}
        />
          <Button
          onClick={showCroppedImage}
          variant="contained"
          color="primary"
         >
          Next
        </Button>
    </>: 
        
      <>
      {
        isLoader === true ? <BigGifLoader /> : 
        <div className="container2">
        <form method="POST" className="form-signin" onSubmit={SigninSubmit}>
          <div className="header">
            <img src="../pics/logo.png" alt="" />
            <span className="app-name">
              <h1>SNAPPY</h1>
            </span>
          </div>
          <div className="avatar-container" onClick={triggerImagePopup}>
            <div className="avatar-image">
              <Avatar src={croppedImage} sx={{ width: 75, height: 75 }} />
            </div>
            <div className="avatar-select">
              <AddPhotoAlternateIcon />
            </div>
          </div>
          <input type="file" accept='image/*' onChange={imageHandler} id="fileBtn" placeholder='Add Avatar' className="input" ref={inputRef} style={{display: "none"}} />
          <input
            type="text"
            name="username"
            className="input"
            placeholder="Username"
            value={username}
            onChange={(e)=> setUserName(e.target.value)}
            required
          />
          <input
            type="email"
            name="email"
            className="input"
            placeholder="Email"
            value={email}
            onChange={(e)=> setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            name="password"
            className="input"
            placeholder="Password"
            value={password}
            onChange={(e)=> setPassWord(e.target.value)}
            required
          />
          <input
            type="submit"
            className="btn"
            value="Sign in"
          />
          <div className="form-text">
            <span className="nor-text">Have an Account ?</span>
            <span className="acc-text">
              <NavLink className="login-link" to="/">
                Login
              </NavLink>
            </span>
            <ToastContainer  />
          </div>
        </form>
      </div>}
      </>
  }
    </>
  );
};

export default Login;
