import React, { useEffect } from 'react'
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { deletePost } from '../../Actions/Post'
import { followUnfollow, loadUser } from '../../Actions/User'
import './model.css'

const Model = ({closeModel, postId, ownerId}) => {

    const dispatch = useDispatch();
    const [postUser, setPostUser] = useState()
    const [isFollow, setIsFollow] = useState(false)

    const following = useSelector(state => state.user.user.following)

    useEffect(()=>{
        setPostUser(localStorage.getItem('user._id'))
        for(let i=0; i<following.length; i++){
            if(following[i] === ownerId){
                setIsFollow(true)
            }
        }
    }, [postId])

    const deletePostHendler = async() =>{
        await dispatch(deletePost(postId))
        closeModel();
        window.location = '/account'
    }

    const followhandler = async () =>{
        await dispatch(followUnfollow(ownerId))
        setIsFollow(!isFollow)
        closeModel()
    }

  return (
    <>
        <div className="model-conatiner">
            {
                postUser && 
                postUser === ownerId ? 
                <>
                <NavLink to={`/post/update/${postId}`} >
                <div className="update-popup popup" >
                    Update caption 
                </div>
                </NavLink>
                <div className="Delete-popup popup" onClick={deletePostHendler}>
                    Delete post 
                </div> 
                </>
                :
                <>
                <div className="Report-popup popup">
                    Report post
                </div>
                <div className="Unfollow-popup popup" onClick={followhandler}>
                    {
                        isFollow ? "Unfollow" : "Follow"
                    }
                </div>
                <div className="favorites-popup popup">
                    Add to favorites
                </div>
                </> 
                
            }
            <div className="Go-popup popup" onClick={closeModel}>
                Go to post
            </div>
            <div className="Share-popup popup">
                Share to...
            </div>
            <div className="Copy-popup popup">
                Copy link
            </div>
            <div className="Cancel-popup" onClick={closeModel}>
                Cancel
            </div>
        </div>
    </>
  )
}

export default Model