export const allCommentReducer = createReducer(initialState, {
    allCommentRequest: (state)=>{
        state.loading = true
    },
    allCommentSuccess: (state, action)=>{
        state.loading = false
        state.comments = action.payload
        state.caption = action.caption
        state.owner = action.owner
        state.ownerName = action.ownerName
        state.ownerImage = action.ownerImage
    },
    allCommentFailuer: (state, action)=>{
        state.loading = true
        state.error = action.payload
    },
})

