import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {useParams, useNavigate } from 'react-router-dom';
import { getSinglePost } from '../../Actions/Post';
import Post from '../Home/Post/Post';
import ClearIcon from '@mui/icons-material/Clear';
import './singlePost.css'
import BigGifLoader from '../Loder/BigGifLoader';

const SinglePost = ({topLoader}) => {

    const params = useParams();
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [isLoader, setIsLoader] = useState(false)
    const post = useSelector(state => state.singlePost.post)

    useEffect(()=>{
        topLoader(40)
        const fetchData = async()=>{
          setIsLoader(true)
          await dispatch(getSinglePost(params.postId))
          topLoader(100)
          setIsLoader(false)
        }
        fetchData();
        return () =>{
          dispatch({type: "singlePostClear"})
        }
    }, [dispatch,])

  return (
    <div className='single-post-main'>
        <div className="likeby-header he">
            <div className="back-likedby" onClick={()=> navigate(-1, {replace: false})}>
                <ClearIcon className='close-likedBy'/>
            </div>
            <div className="likedBy-head">
               Post
            </div>
        </div>
        {
          isLoader === true ? <BigGifLoader /> :
            post && (<Post 
            key={post._id}
            caption={post.caption}
            postImage={post.image.public_url}
            likes={post.likes}
            comments={post.comments}
            ownerImage={post.owner.avatar.url}
            ownerName={post.owner.username}
            ownerId={post.owner._id}
            postId={post._id}
            postHeight={post.postHeight}
            comp="singPost"
          /> )
        }
    </div>
  )
}

export default SinglePost